"use client";

import Link from "next/link";
import {
  MdSchedule,
  MdAttachMoney,
  MdTrendingUp,
  MdCalendarToday,
  MdChevronRight,
  MdStar,
} from "react-icons/md";
import { formatCents } from "@/lib/utils";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { CommissionBreakdownCard } from "./CommissionBreakdownCard";
import { StatCard } from "@/components/ui/stat-card";
import { StatCardGridSkeleton } from "@/components/ui/skeletons";
import { useTabStore } from "@/hooks/use-tab-store";

interface SellerStats {
  totalEarnings: number;
  pendingEarnings: number;
  totalSessions: number;
  pendingRequests: number;
  averageRating: number | null;
  totalReviews: number;
  hourlyRate: number;
}

interface RecentSession {
  id: string;
  topic: string;
  scheduledAt: string | null;
  status: string;
  buyer: {
    id: string;
    name: string | null;
    avatar: string | null;
  };
}

interface SellerDashboardTabProps {
  experienceId?: string;
  stats: SellerStats;
  recentSessions: RecentSession[];
  isLoading?: boolean;
  standalone?: boolean; // When true, uses /app paths instead of /experiences
}

export function SellerDashboardTab({
  experienceId,
  stats,
  recentSessions,
  isLoading,
  standalone,
}: SellerDashboardTabProps) {
  const { setTab } = useTabStore();

  // Generate URLs for non-standalone mode
  const requestsUrl = `/experiences/${experienceId}?tab=requests&mode=seller`;
  const settingsUrl = `/experiences/${experienceId}?tab=settings&mode=seller`;
  const analyticsUrl = `/experiences/${experienceId}?tab=analytics&mode=seller`;
  const sessionsUrl = `/experiences/${experienceId}?tab=sessions&mode=seller`;

  if (isLoading) {
    return <DashboardLoading />;
  }

  return (
    <div className="p-4 sm:p-6 md:p-8">
      {/* Header */}
      <div className="mb-6">
        <h1 className="text-2xl font-semibold text-foreground">Dashboard</h1>
        <p className="text-muted-foreground mt-1">
          Overview of your seller activity
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
        <StatCard
          icon={MdAttachMoney}
          label="Total Earnings"
          value={formatCents(stats.totalEarnings)}
          color="success"
        />
        <StatCard
          icon={MdSchedule}
          label="Pending"
          value={formatCents(stats.pendingEarnings)}
          color="warning"
        />
        <StatCard
          icon={MdCalendarToday}
          label="Sessions"
          value={stats.totalSessions}
          color="primary"
        />
        <StatCard
          icon={MdStar}
          label="Rating"
          value={stats.averageRating?.toFixed(1) || "N/A"}
          color="warning"
        />
      </div>

      {/* Quick Actions & Recent Activity */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Pending Requests */}
        <Card>
          <CardHeader className="flex flex-row items-center justify-between">
            <CardTitle className="text-lg">Pending Requests</CardTitle>
            {stats.pendingRequests > 0 && (
              <Badge variant="secondary">{stats.pendingRequests}</Badge>
            )}
          </CardHeader>
          <CardContent>
            {stats.pendingRequests === 0 ? (
              <div className="text-center py-6">
                <p className="text-muted-foreground">No pending requests</p>
                <p className="text-sm text-muted-foreground mt-1">
                  New booking requests will appear here
                </p>
              </div>
            ) : (
              <div className="space-y-3">
                {recentSessions
                  .filter((s) => s.status === "REQUESTED")
                  .slice(0, 3)
                  .map((session) => (
                    <div
                      key={session.id}
                      className="flex items-center gap-3 p-3 rounded-lg bg-muted/50"
                    >
                      <Avatar className="h-10 w-10">
                        <AvatarImage src={session.buyer.avatar || undefined} />
                        <AvatarFallback>
                          {session.buyer.name?.[0]?.toUpperCase() || "?"}
                        </AvatarFallback>
                      </Avatar>
                      <div className="flex-1 min-w-0">
                        <p className="font-medium text-foreground truncate">
                          {session.buyer.name || "Anonymous"}
                        </p>
                        <p className="text-sm text-muted-foreground truncate">
                          {session.topic}
                        </p>
                      </div>
                      <Button size="sm">Review</Button>
                    </div>
                  ))}
                {standalone ? (
                  <Button variant="outline" className="w-full mt-2" onClick={() => setTab("requests")}>
                    View All Requests
                  </Button>
                ) : (
                  <Link href={requestsUrl}>
                    <Button variant="outline" className="w-full mt-2">
                      View All Requests
                    </Button>
                  </Link>
                )}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Your Rate */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Your Rate</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center justify-between mb-4">
              <div>
                <p className="text-3xl font-bold text-primary">
                  {formatCents(stats.hourlyRate)}/hr
                </p>
                <p className="text-sm text-muted-foreground mt-1">
                  Current hourly rate
                </p>
              </div>
              <div className="h-12 w-12 rounded-full bg-primary/10 flex items-center justify-center">
                <MdTrendingUp className="h-6 w-6 text-primary" />
              </div>
            </div>
            <div className="flex gap-2">
              {standalone ? (
                <Button variant="outline" className="flex-1" onClick={() => setTab("settings")}>
                  Update Rate
                </Button>
              ) : (
                <Link href={settingsUrl} className="flex-1">
                  <Button variant="outline" className="w-full">
                    Update Rate
                  </Button>
                </Link>
              )}
              {standalone ? (
                <Button className="flex-1" onClick={() => setTab("analytics")}>
                  View Analytics
                </Button>
              ) : (
                <Link href={analyticsUrl} className="flex-1">
                  <Button className="w-full">View Analytics</Button>
                </Link>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Commission Breakdown */}
        <CommissionBreakdownCard />
      </div>

      {/* Recent Sessions */}
      <Card className="mt-6">
        <CardHeader className="flex flex-row items-center justify-between">
          <CardTitle className="text-lg">Recent Sessions</CardTitle>
          {standalone ? (
            <Button variant="ghost" size="sm" className="gap-1" onClick={() => setTab("offers")}>
              View All
              <MdChevronRight className="h-4 w-4" />
            </Button>
          ) : (
            <Link href={sessionsUrl}>
              <Button variant="ghost" size="sm" className="gap-1">
                View All
                <MdChevronRight className="h-4 w-4" />
              </Button>
            </Link>
          )}
        </CardHeader>
        <CardContent>
          {recentSessions.length === 0 ? (
            <div className="text-center py-6">
              <p className="text-muted-foreground">No sessions yet</p>
              <p className="text-sm text-muted-foreground mt-1">
                Completed sessions will appear here
              </p>
            </div>
          ) : (
            <div className="space-y-3">
              {recentSessions.slice(0, 5).map((session) => (
                <div
                  key={session.id}
                  className="flex items-center gap-3 p-3 rounded-lg hover:bg-muted/50 transition-colors"
                >
                  <Avatar className="h-10 w-10">
                    <AvatarImage src={session.buyer.avatar || undefined} />
                    <AvatarFallback>
                      {session.buyer.name?.[0]?.toUpperCase() || "?"}
                    </AvatarFallback>
                  </Avatar>
                  <div className="flex-1 min-w-0">
                    <p className="font-medium text-foreground truncate">
                      {session.buyer.name || "Anonymous"}
                    </p>
                    <p className="text-sm text-muted-foreground truncate">
                      {session.topic}
                    </p>
                  </div>
                  <Badge
                    variant={
                      session.status === "COMPLETED"
                        ? "secondary"
                        : session.status === "ACCEPTED"
                          ? "default"
                          : "outline"
                    }
                  >
                    {session.status}
                  </Badge>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

function DashboardLoading() {
  return (
    <div className="p-4 sm:p-6 md:p-8">
      <div className="mb-6">
        <div className="h-8 w-48 bg-muted rounded animate-pulse" />
        <div className="h-4 w-64 mt-2 bg-muted rounded animate-pulse" />
      </div>

      <StatCardGridSkeleton count={4} className="mb-8" />

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card variant="bordered">
          <CardContent className="p-6">
            <div className="h-32 w-full bg-muted rounded animate-pulse" />
          </CardContent>
        </Card>
        <Card variant="bordered">
          <CardContent className="p-6">
            <div className="h-32 w-full bg-muted rounded animate-pulse" />
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
